.inner {
  margin: 0 auto;
  padding: 0 2rem;
  position: relative;
  width: 70%;
}

.grid-container {
  width: 70%;
  display: grid;
  margin: 0 auto;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 10px;
}

.btn {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

.btn button {
  margin-right: 10px;
}

.btn button:last-child {
  margin-right: 0;
}

span.pagination {
  right: 0;
  position: absolute;
}